.navigation {
  display: flex;
  position: absolute;
  top: 0;
  width: 90%;
  height: 50%;
  padding: 10px 0;
  flex-wrap: wrap;

  .navigation-header {
    width: 100%;
    height: 70%;
    display: flex;
    align-items: center;

    select {
      max-width: 20vw;
      max-height: 3vw;
      font-size: 1.5vw;
      margin: 1vw;
      padding: 0.2vw 1vw;
      background: transparent;
      color: white;
      border: 1px solid #7c7c7c;
      cursor: pointer;
      border-radius: 3px;

      option {
        background: rgba(128, 128, 128, 30%);
      }
    }

    .svg-inline--fa {
      overflow: visible;
      box-sizing: content-box;
      width: 4vw;
      height: 2vw;
      border-radius: 0.5vw;
      background: -webkit-gradient(
        linear,
        left top,
        left bottom,
        from(rgba(255, 255, 255, 0.34)),
        to(#e6e6e6)
      );
      border-color: #7c7c7c;
      cursor: pointer;
    }

    .play {
      margin-right: 1vw;
    }

    .hide {
      display: none;
    }

    .delete {
      background: -webkit-gradient(
        linear,
        left top,
        left bottom,
        from(rgba(255, 255, 255, 0.34)),
        to(#e6e6e6)
      );
      border-color: #7c7c7c;
      color: black;

      &:active {
        color: rgba(255, 0, 0, 0.68);
      }
    }
  }

  .switches {
    display: flex;
    justify-content: space-around;
    color: white;
    width: 100%;
    height: 50%;

    .signatures-toggle {
      display: flex;
      align-items: center;
      gap: 0.5vw;
      width: 30%;

      .signature-button {
        font-size: 1vw;
        cursor: pointer;
        width: 3vw;
        height: 3vw;
        border-radius: 50%;
        border-color: #7c7c7c;
        background: -webkit-gradient(
          linear,
          left top,
          left bottom,
          from(rgba(255, 255, 255, 0.34)),
          to(#e6e6e6)
        );
        padding: 0;
      }
    }

    .volume-change {
      display: flex;
      align-items: center;
      width: 30%;

      span {
        margin-right: 1vw;
      }

      .volumeSlider.MuiSlider-root {
        color: grey;
        width: 15vw;
      }
    }

    .record {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40%;

      span {
        padding: 0 2px 0 5px;
      }

      .button {
        cursor: pointer;
        width: 3vw;
        height: 3vw;
        border-radius: 50%;
        border-color: #7c7c7c;
        background: -webkit-gradient(
          linear,
          left top,
          left bottom,
          from(rgba(255, 255, 255, 0.34)),
          to(#e6e6e6)
        );
      }

      .record-button {
        border-color: #7c7c7c;
        background: linear-gradient(
          0deg,
          rgba(255, 27, 0, 1) 0%,
          rgba(251, 75, 2, 1) 100%
        );
      }
    }
  }

  .save {
    display: flex;
    align-items: center;

    span {
      padding: 0 5px;
    }

    input.title {
      z-index: 2;
      width: 5vw;
      height: 1.8vw;
      margin-right: 5px;
      font-size: 1.5vw;

      &::placeholder {
        font-size: 1vw;
        color: grey;
      }
    }
  }
}
